	import {guessYouLikeList, productCate, productList} from "@/api/product";
	import rfLoadMore from '@/components/rf-load-more/rf-load-more';
	import rfRecommend from '@/components/rf-recommend/rf-recommend';
	import empty from '@/components/empty';
	import uniIcons from '@/components/uni-icons/uni-icons';
	import rfSearchBar from '@/components/rf-search-bar/rf-search-bar';

	export default {
		components: {
			rfLoadMore,
			rfSearchBar,
			empty,
			uniIcons,
			rfRecommend,
		},
		data() {
			return {
				loading: true,  // 加载动画
				headerShow: true,
				navBarTop: "0px",
				cateMaskState: 0, //分类面板展开状态
				headerPosition: "fixed",
				headerTop: "0px",
				loadingType: 'more', //加载更多状态
				filterIndex: 0,
				cateId: 0, //已选三级分类id
				priceOrder: 0, //1 价格从低到高 2价格从高到低
				salesOrder: 0, //1 价格从低到高 2价格从高到低
				cateList: [],
				goodsList: [],  // 商品列表
				keyword: null,  // 搜索关键字
				cateParams: null,
				page: 1,
				filterParams: {},
				isShowNavBar: true,
				hotSearchDefault: '请输入关键字',
				contentTop: '88upx',
        errorInfo: undefined, // 接口请求出错信息
			};
		},
		onLoad(options) {
			this.initData(options);
		},
		onShow() {
			if (navigator) {
				this.headerTop = document.getElementsByTagName('uni-page-head')[0] && document.getElementsByTagName('uni-page-head')[0].offsetHeight + 'px';
			}
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			if (e.scrollTop >= 0) {
				this.headerPosition = "fixed";
			} else {
				this.headerPosition = "absolute";
			}
		},
		//下拉刷新
		onPullDownRefresh() {
			this.filterParams = {};
			this.page = 1;
			this.goodsList = [];
			this.getProductList('refresh');
		},
		//加载更多
		onReachBottom() {
			this.page++;
			this.getProductList();
		},
		methods: {
			// 初始化数据
			initData(options) {
				if (navigator) {
					this.headerTop = document.getElementsByTagName('uni-page-head')[0] && document.getElementsByTagName('uni-page-head')[0].offsetHeight + 'px';
				}











				this.navBarTop = '88upx';

				this.cateId = options.cate_id;
				if (options.params) {
					this.cateParams = JSON.parse(options.params);
					if (this.cateParams.guessYouLike === 1) {
						this.isShowNavBar = false;
						this.contentTop = '18upx';
						this.getGuessYouLikeList();
						return;
					}
				}
				this.keyword = options.keyword;
				this.hotSearchDefault = options.keyword;
				if (!this.keyword) {
					this.hotSearchDefault = uni.getStorageSync('hotSearchDefault')
				}
				this.getProductList();
			},
      // 搜索商品列表
			handleSearchProductList(e) {
				this.keyword = e.searchValue;
				this.page = 1;
				this.goodsList = [];
				this.filterParams = {};
				this.getProductList();
			},
      // 跳转至主页
			navToIndex() {
				uni.reLaunch({
					url: `/pages/index/index`
				})
			},
			// 获取商品列表
			async getProductList(type) {
				let params = {}
				if (this.keyword) {
					params.keyword = this.keyword
				} else if (this.cateId) {
					params.cate_id = this.cateId
				} else if (this.cateParams) {
					params = {...this.cateParams}
				}
				params.page = this.page;
				await this.$get(`${productList}`, {
					...params,
					...this.filterParams
				}).then(r => {
          this.loading = false;
					this.getProductCate();
					if (type === 'refresh'){
            uni.stopPullDownRefresh();
					}
					this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
					this.goodsList = [...this.goodsList, ...r.data];
				}).catch(err => {
					this.errorInfo = err;
					this.goodsList = [];
          this.loading = false;
					if (type === 'refresh') {
						uni.stopPullDownRefresh();
					}
				});
			},
			async getGuessYouLikeList() {
				await this.$get(`${guessYouLikeList}`, {}).then(r => {
					this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
					this.goodsList = [...this.goodsList, ...r.data];
				}).catch((err) => {
					console.log(err)
				});
			},
			// 获取商品分类
			async getProductCate() {
				await this.$get(`${productCate}`).then(r => {
					this.cateList = r.data
					this.cateList.unshift({
						title: '全部商品',
						id: ''
					})
				})
			},
			//筛选点击
			tabClick(index) {
				this.filterParams = {}
				if (this.filterIndex === index && index !== 4 && index !== 1) {
					return;
				}
				this.filterIndex = index;
				if (index === 0) {
					this.filterParams = {}
				} else if (index === 1) {
					if (this.salesOrder === 1) {
						this.filterParams.sales = 'desc'
						this.salesOrder = 2;
					} else {
						this.filterParams.sales = 'asc'
						this.salesOrder = 1;
					}
				} else if (index === 2) {
					this.filterParams.view = 'desc';
				} else if (index === 3) {
					this.filterParams.collect = 'desc';
				} else if (index === 4) {
					if (this.priceOrder === 1) {
						this.filterParams.price = 'desc'
						this.priceOrder = 2;
					} else {
						this.filterParams.price = 'asc'
						this.priceOrder = 1;
					}
				}
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.page = 1;
				this.goodsList.length = 0;
        this.loading = true;
				this.getProductList();
			},
			//显示分类面板
			toggleCateMask(type) {
				let timer = type === 'show' ? 10 : 300;
				let state = type === 'show' ? 1 : 0;
				this.cateMaskState = 2;
				setTimeout(() => {
					this.cateMaskState = state;
				}, timer)
			},
			//分类点击
			changeCate(id) {
				if (!id) {
					this.cateParams = null;
					this.cateId = null;
					this.keyword = null;
				}
				this.cateId = id;
				this.keyword = undefined;
				this.toggleCateMask();
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.page = 1;
				this.goodsList.length = 0;
				this.filterParams = {};
        this.loading = true;
				this.getProductList('refresh');
			},
			//跳转详情
			navTo(url) {
				uni.navigateTo({
					url
				})
			},
			stopPrevent() {
			}
		},
	}
